.main-notification{
  .main2__img{
    width: 53em;
  }
  .main2__content{
    max-width: 51em;
  }
}

.notification{
  &__grid{
    margin-top: 2.8125em;
    display: grid;
    grid-template-columns: 12.1875em calc((100% - 12.1875em - 3.75em) / 2) calc((100% - 12.1875em - 3.75em) / 2);
    gap: 1.875em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    padding: 1.5em;
    border: 1px solid #F0F2F3;
    border-radius: .625em;
  }
  &__desc1{
    @media screen and (min-width: 993px){
      //display: flex;
      //flex-direction: column;
      //justify-content: center;
    }
  }
  .text{
    color: $dark;
    li,
    p{
      font-size: .9375em;
      line-height: 153%;
    }
    h4{
      margin-bottom: .4em;
    }
  }
  &.gray-bg{
    .notification__desc{
      background-color: $white;
      border-color: $white;
    }
  }
}

.diffusion{
  &__desc{
    max-width: 58em;
    color: $dark;
    margin-top: 1.5em;
  }
  &__wrap{
    padding: 2.1875em;
    background-color: #40464c;
    background-image: url(../img/diffusion-bg.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 27.625em auto;
    border-radius: .75em;
    margin-top: 1.5em;
  }
  .text{
    color: #c2c8cf;
    h4{
      margin-bottom: .5em;
    }
    p{
      font-size: .9375em;
      line-height: 153%;
    }
  }
}


@media screen and (max-width: 992px){
  .main-notification{
    .main2__img{
      width: 29em;
    }
    .main2__content{
      max-width: 29.5em;
    }
  }

  .notification{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 12.1875em calc(100% - 12.1875em);
      gap: 1em 0;
      align-items: center;
    }
    &__desc{
      padding: .875em;
    }
    &__desc1{
      @media screen and (min-width: 577px){
        padding: 1.5em;
        border: none;
      }
    }
    &__desc2{
      grid-column: 1/-1;
    }
  }

  .diffusion{
    &__desc{
      margin-top: 1em;
    }
    &__wrap{
      padding: 1.25em;
      border-radius: .625em;
      margin-top: 1.25em;
      background-image: url(../img/diffusion-bg2.svg);
      background-size: 25.375em auto;
    }
    .text{
      p{
        font-size: .875em;
        line-height: 150%;
      }
    }
  }

  .info-notification{
    .h2{
      max-width: 15em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-notification{
    .main2__img{
      top: unset;
      left: -1.5em;
      bottom: -.75em;
      transform: unset;
    }
    .main2__inner{
      padding-bottom: 11.75em;
    }
  }

  .notification {
    &__grid {
      grid-template-columns: 100%;
    }
    &__img{
      width: 12.25em;
      margin: 0 auto;
    }
  }

  .diffusion{
    &__wrap{
      margin-top: 1em;
      background-image: url(../img/diffusion-bg3.svg);
      background-position: center bottom 0;
      background-size: 22.5em auto;
    }
  }
}
