.main-asset-protection{
  .main2__img{
    width: 47.5em;
  }
}

.complex{
  border-top: 1px solid #ebebeb;
  padding-top: 5.625em;
  .advantages__wrap{
    background: $black;
    color: $white;
  }
  @media screen and (min-width: 993px){
    .advantages__inner{
      grid-template-columns: repeat(3,calc(100% / 3 - 17.5em / 3));
      column-gap: 8.75em;
    }
    .advantage-card{
      &:not(:last-child){
        position: relative;
        &:after{
          content: '';
          position: absolute;
          top: 50%;
          left: 100%;
          transform: translate(-50%,-50%);
          height: 9.5em;
          width: 1px;
          background-color: rgba($white,.1);
          margin-left: 4.375em;
        }
      }
    }
  }
  &__top{
    margin-bottom: 2.5em;
    @include grid100gap(1.5em);
    max-width: 58.25em;
  }
  &__desc{
    color: #4d4d4d;
  }
}

.is-wanted2{
  @media screen and (min-width: 993px){
    .is-wanted__inner{
      grid-template-columns: 40.5em calc(100% - 40.5em - 1.875em);
    }
    .step-card__desc{
      max-width: 32.25em;
    }
  }
}

.court{
  background-color: $grayBg;
  background-image: url(../img/court-bg.svg);
  background-repeat: no-repeat;
  background-size: 45.875em auto;
  background-position: right 8.75em top 5em;
  &__top{
    @include grid100gap(1.25em);
    max-width: 57.5em;
  }
  &__grid{
    background-color: $white;
    display: grid;
    grid-template-columns: 50% 50%;
    border-radius: 1.0625em;
    margin-top: 1.875em;
  }

}

.court-card{
  padding: 2.5em;
  @media screen and (min-width: 577px){
    &:nth-child(odd){
      border-right: 1px solid #f2f2f2;
    }
  }
  .text{
    margin-top: .8125em;
    &:first-child{
      margin-top: 0;
    }
    p,
    li{
      font-size: .875em;
      line-height: 164%;
    }
    ul,ol{
      margin: .3125em 0;
      gap: .3125em;
    }
    p{
      @include lastM(0 0 .3em);
    }
  }
}

.info-card2{
  .text{
    color: $black;
  }
  @media screen and (min-width: 993px){
    grid-template-columns: 42.25em 33.5em;
    justify-content: space-between;
    .text{
      p,li{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main-asset-protection{
    .main2__img{
      left: 25em;
      width: 26em;
      ._view1{
        display: block;
      }
    }
  }

  .complex {
    padding-top: 1.875em;
    .advantages__wrap {
      width: calc(100% - 2.5em);
    }
    &__top{
      margin-bottom: 1.25em;
      gap: .625em;
    }
  }

  .is-wanted2{
    .step-card{
      &__top{
        justify-content: unset;
        align-items: center;
      }
      &__icon{
        margin: 0 .5em 0 0;
        width: 2em;
        order: -1;
        svg{
          width: 100%;
          height: auto;
        }
      }
      &__desc{
        margin: 0;
        padding-left: 2.5em;
      }
    }
  }

  .court{
    background-image: unset;
    &__top{
      gap: .625em;
    }
    &__grid{
      border-radius: .625em;
    }
  }

  .court-card{
    padding: 1.25em;
    .text{
      margin-top: .625em;
      p,
      li{
        font-size: .8125em;
        line-height: 161%;
      }
      ul{
        padding-left: .25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-asset-protection{
    .main2__inner{
      padding-bottom: 13.75em;
    }
    .main2__img{
      top: unset;
      bottom: 0;
      left: 50%;
    }
  }

  .complex {
    .advantages__wrap {
      width: calc(100% - 1.875em);
    }
  }

  .court{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .court-card{
    padding: .875em;
    border-bottom: 1px solid #f2f2f2;
    &:last-child{
      border: none;
    }
  }
}
