.protection{
  &__top{
    max-width: 42.5em;
    @include grid100gap(1.5em);
  }
  .h2{
    max-width: 10em;
  }
}

.protection-slider{
  margin-top: 2.5em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 1.875em;
    }
    &-nav{
      position: absolute;
      right: -.3125em;
      bottom: 100%;
      display: flex;
      margin-bottom: 2.5em;
    }
    &-button{
      position: static;
      margin: 0 .3125em;
    }
  }
}


@media screen and (max-width: 992px){
  .protection{
    overflow: hidden;
    &__top{
      max-width: 100%;
      gap: .625em;
    }
    .h2{
      max-width: 100%;
    }
  }

  .protection-slider{
    margin-top: 1.25em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        width: 17.875em;
        padding-right: .9375em;
      }
      &-nav{
        display: none;
      }
    }
    @media screen and (min-width: 577px){
