.text-page{
  padding: 2.5em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 19.25em - 1.875em) 19.25em;
    gap: 1.875em;
    margin-top: 1.875em;
  }
  .text{
    h3{
      margin-bottom: .38em;
    }
    p,li{
      font-size: 1em;
      line-height: 150%;
    }
    p{
      @include lastM(0 0 .625em);
    }
    ul,ol{
      margin: .625em 0;
    }
    hr{
      height: .75em;
    }
  }
}

.text-nav{
  position: sticky;
  top: 1em;
  border: 1px solid #f0f2f3;
  border-radius: .625em;
  &__link{
    padding: .875em 1.25em;
    display: flex;
    font-weight: 500;
    color: rgba($black,.8);
    position: relative;
    &._active{
      color: $black;
      &:before{
        opacity: 1;
      }
    }
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 3px;
      height: 100%;
      background-color: $primary;
      transition: all .3s;
      opacity: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .text-page{
    padding: 2.5em 0;
    &__grid{
      grid-template-columns: calc(100% - 15.625em) 14.375em;
      gap: .625em 1.25em;
    }
    .text{
      p,li{
        font-size: .875em;
      }
      hr{
        height: .25em;
      }
    }
  }

  .text-nav{
    &__link{
      padding: .625em 1.25em;
      span{
        font-size: .875em;
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .text-page{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      order: -1;
    }
  }
}
