.text-underline{
  text-decoration: underline;
}

.red-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.dark-text{
  color: $dark;
}

.text h1,
.h1{
  font-weight: 500;
  font-size: 3.6875em;
  line-height: 119%;
}

.text h2,
.h2{
  font-weight: 500;
  font-size: 3.125em;
  line-height: 114%;
}

.text h3,
.h3{
  font-weight: 500;
  font-size: 1.625em;
  line-height: 127%;
  letter-spacing: -0.02em;
}

.text h4,
.h4{
  font-weight: 500;
  font-size: 1.375em;
  line-height: 127%;
}

.text h5,
.h5{
  font-weight: 600;
  font-size: 1.0625em;
  line-height: 129%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 153%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 153%;
}

.text14{
  font-size: .875em;
  line-height: 150%;
}

.text13{
  font-size: .8125em;
  line-height: 162%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}



@media screen and (max-width: 992px){
  .text h1,
  .h1{
    font-size: 2.0625em;
  }

  .text h2,
  .h2{
    font-size: 1.75em;
  }

  .text h3,
  .h3{
    font-size: 1.0625em;
  }

  .text h4,
  .h4{
    font-size: 1.1875em;
  }

  .text h5,
  .h5{
    font-size: 1em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px) {
  .text h1,
  .h1 {
    font-size: 1.5625em;
  }

  .text h2,
  .h2 {
    font-size: 1.375em;
  }

  .text h3,
  .h3 {
    font-size: 1.125em;
  }

  .text h4,
  .h4 {
    font-size: 1.0625em;
  }

  .text h5,
  .h5 {
    font-size: .9375em;
  }
}


@media screen and (max-width: 576px){
  .text14-mob{
    font-size: .875em;
  }
}
