@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.25em;
    height: 4.0625em;
    border-radius: 56.25em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-small{
    height: 3.0625em;
    padding: 0 1.75em;
    span{
        font-size: .9375em;
    }
}

.m-btn-arrow{
    img{
        width: 1.5em;
        margin-left: 1em;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: $black;
        color: $white;
        box-shadow: 0 11px 34px 0 rgba(26, 26, 26, 0.31);
    }
}

.m-btn-black-outline{
    border: 2px solid $black;
    color: $black;
    &:hover{
        background-color: $black;
        color: $white;
    }
}

.m-btn-red-outline{
    border: 2px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-arrow2{

}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.5625em;
        padding: 0 1.5em;
        span{
            font-size: .875em;
        }
    }

    .m-btn-small{
        &:not(._type2){
            height: 2.3125em;
            padding: 0 1.0625em;
            span{
                font-size: .8125em;
            }
        }
    }

    .m-btn-arrow{
        img{
            margin-left: .625em;
        }
    }
}


