.info{
  padding: 2.625em 0 5.75em;
}

.info-card{
  margin-bottom: 2.625em;
  display: grid;
  grid-template-columns: calc(100% - 30.625em - 2.5em) 30.625em;
  gap: 2.5em;
  align-items: center;
  @media screen and (min-width: 993px){
    &:nth-child(even){
      grid-template-columns: 30.625em calc(100% - 30.625em - 2.5em);
      .info-card__img{
        order: -1;
      }
    }
  }
  &:last-child{
    margin-bottom: 0;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.8125em;
    }
    &--mob{
      display: none;
    }
  }
  .h2{
    margin-bottom: .5em;
  }
  .text{
    p{
      font-size: .9375em;
    }
  }
}

.law{
  &__desc{
    color: #444547;
    max-width: 57.25em;
    margin-top: 1.5em;
  }
  &__items{
    margin-top: 2.5em;
  }
}

.qa-card{
  &._open{
    ._hide{
      opacity: 0;
    }
  }
  &:first-child{
    border-top: 1px solid #cbcbcb;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em;
    border-bottom: 1px solid #cbcbcb;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__desc{
    margin-top: .33em;
    color: #434343;
  }
  &__hidden{
    display: none;
    padding: 1.25em;
    h5{
      margin-bottom: .625em;
    }
    ol{
      gap: 0;
      margin-top: 0;
      margin-bottom: .75em;
    }
  }
}

.main2{
  overflow: hidden;
  &__inner{
    padding: 5em 0;
    position: relative;
  }
  &__content{
    max-width: 53.75em;
    position: relative;
    z-index: 2;
  }
  .breadcrumbs{
    margin-bottom: 1.5em;
  }
  &__desc{
    margin-top: 1.5em;
    color: #444547;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 50em;
    width: 45.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view3,
    ._view2{
      display: none;
    }
  }
  &__logo{
    position: absolute;
    z-index: 2;
    width: 27.5em;
    height: 16.25em;
    @include center;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .info{
    padding: 1.875em 0;
  }

  .info-card{
    margin-bottom: 3.75em;
    display: block;
    @media screen and (min-width: 577px){
      &:nth-child(even){
        .info-card__img{
          float: left;
          margin: 0 1em .5em 0;
        }
      }
    }
    &__img{
      &--pc{
        display: none;
      }
      &--mob{
        display: block;
        float: right;
        width: 20.625em;
        margin: 0 0 .5em 1em;
      }
    }
    .text{
      p{
        font-size: .875em;
      }
    }
  }

  .law{
    &__desc{
      margin-top: .625em;
    }
  }

  .qa-card{
    &__show{
      padding: .625em 1.25em;
    }
    &__hidden{
      padding: .625em;
    }
    &__title{
      font-size: 1.0625em;
      line-height: 124%;
    }
    &__desc{
      font-size: .875em;
      line-height: 150%;
      margin-top: .71em;
    }
    &__btn{
      margin-left: 0;
    }
  }

  .main2{
    &__inner{
      padding: 2.5em 0;
    }
    &__content{
      max-width: 33.625em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__img{
      left: 23.875em;
      width: 30.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__logo{
      width: 10em;
      height: 10em;
      right: 3.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .info-card{
    margin-bottom: 1.875em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    &__img{
      &--pc{
        display: block;
      }
      &--mob{
        display: none;
      }
    }
  }

  .main2{
    &__inner{
      text-align: center;
      padding-bottom: 9.25em;
    }
    &__img{
      left: 50%;
      transform: translateX(-50%);
      top: 100%;
      margin-top: -8em;
    }
    &__logo{
      top: unset;
      bottom: 2em;
      right: 50%;
      transform: translateX(50%);
    }
  }

  .qa-card{
    &__desc{
      margin-top: .29em;
    }
  }
}
