.main-human-rights3{
  .main2__inner{
    padding: 6.25em 0;
  }
  .main2__desc{
    max-width: 47.5em;
  }
  .main2__img{
    @media screen and (min-width: 993px){
      left: 46em;
    }
  }
}

.mission{
  background-color: $grayBg;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 33.5em - 1.875em) 33.5em;
    gap: 1.875em;
    align-items: center;
  }
  .h2{
    max-width: 12.25em;
  }
  .text{
    margin-top: 1.5em;
    max-width: 41.875em;
  }
  &__img{
    width: 100%;
    border-radius: 1.875em;
    display: block;
  }
}

.direction{
  .h2{
    max-width: 13.75em;
  }
  .text{
    margin-top: 1.5em;
    p{
      @include lastM(0 0 .6em);
    }
    ul{
      margin: .625em 0;
    }
  }
}

.organization{
  padding-bottom: 8.75em;
  &__wrap{
    max-width: 1488px;
    margin: 0 auto;
    width: 100%;
    position: relative;
    min-height: 23em;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.125em;
    &:after{
      background-color: rgba($black,.73);
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      background-image: url(../img/organization-bg.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 34.375em auto;
      z-index: 3;
    }
    ._view2,
    ._view3{
      display: none;
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__inner{
    padding: 6.25em 0 9.75em;
    position: relative;
  }
  &__text{
    width: 50em;
    max-width: 100%;
    text-align: center;
    margin: 0 auto;
    @include grid100gap(.625em);
    position: relative;
    z-index: 2;
    color: $white;
  }
  &__title{
    font-size: 2.625em;
    line-height: 105%;
    letter-spacing: -0.02em;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 145%;
  }
}

.organization-contacts{
  position: absolute;
  width: 68.625em;
  max-width: 100%;
  left: 50%;
  bottom: -4.25em;
  transform: translateX(-50%);
  padding: 2.5em;
  border: 1px solid #f0f2f3;
  background-color: $white;
  border-radius: 1.25em;
  z-index: 3;
  display: flex;
  justify-content: space-between;
}

.contact-item2{
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .375em;
  }
  &__icon{
    width: 1.125em;
    margin-right: .375em;
  }
  &__title{
    font-weight: 500;
  }
  &__desc{
    color: #373737;
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
  .socials{
    &__item{
      width: 1.625em;
      height: 1.625em;
      @include lastM(0 .625em 0 0);
    }
  }
}


@media screen and (max-width: 992px){
  .main-human-rights3{
    .main2__inner{
      padding: 2.5em 0;
    }
    .main2__content{
      max-width: 31.25em;
    }
    .main2__img{
      ._view1{
        display: block;
      }
    }
    .h1{
      max-width: 11.6em;
    }
  }

  .mission{
    &__grid{
      grid-template-columns: calc(100% - 21.625em) 20.625em;
      gap: 1em;
      align-items: unset;
    }
    &__col2{
      padding-top: 3em;
    }
    .h2{
      max-width: unset;
      width: calc(100% + 5em);
    }
    .text {
      margin-top: 1em;
    }
    &__img{
      border-radius: .625em;
    }
  }

  .direction{
    .h2{
      max-width: unset;
    }
    .text{
      margin-top: 1em;
    }
  }

  .organization{
    padding-bottom: 4.875em;
    &__wrap{
      min-height: 15.25em;
    }
    &__img{
      border-radius:0;
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
      &:before{
        background-image: url(../img/organization-bg2.svg);
        background-position: right 2.75em center;
        background-size: 16.875em auto;
        z-index: 3;
      }
    }
    &__inner{
      padding: 3.125em 0 6.625em;
    }
    &__text{
      width: 31em;
    }
    &__title{
      font-size: 1.75em;
      line-height: 118%;
      letter-spacing: unset;
      font-weight: 500;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .organization-contacts{
    padding: .875em;
    border-radius: .625em;
    bottom: -3em;
  }

  .contact-item2{
    &__top{
      margin-bottom: 0;
    }
    @media screen and (min-width: 577px){
      max-width: 12.25em;
      &__desc{
        font-size: .875em;
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-human-rights3{
    .main2__inner{
      padding-bottom: 13.25em;
    }
    .main2__img{
      margin-top: -11em;
      width: 21.25em;
      top: unset;
      bottom: 0;
      ._view1{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    .h1{
      max-width: 100%;
    }
  }

  .mission{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 0;
    }
    .h2{
      width: 100%;
    }
  }

  .organization{
    padding-bottom: 11.5em;
    &__wrap{
      min-height: 23em;
    }
    &__img{
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
      &:before{
        background-image: url(../img/organization-bg3.svg);
        background-position: center top;
        z-index: 3;
      }
    }
    &__inner{
      padding-bottom: 11.5em;
    }
    &__title{
      font-size: 1.375em;
      line-height: 123%;
      font-weight: 400;
    }
  }

  .organization-contacts{
    @include grid100gap(.625em);
    bottom: -9.625em;
  }
}
