.main-cases{
  .main2__inner{
    padding: 5em 0 2.5em;
  }
  .main2__content{
    max-width: 46.25em;
  }
  .main2__img{
    width: 51.5em;
    left: 42em;
  }
}

.cases-nav{
  padding: 1.25em 0;
  border-bottom: 1px solid #ebebeb;
  border-top: 1px solid #ebebeb;
}

.cases2{
  &__items{
    @include grid100gap(2.8125em);
  }
}

.case-card5{
  border: 1px solid #dfdfdf;
  border-radius: 1em;
  padding: 2.125em;
  &__top{
    @include grid100gap(.625em);
    max-width: 59.75em;
  }
  &__items{
    padding: 1.25em 2.1875em;
    border-radius: 1em;
    margin-top: 1.5em;
    background: #f3f3f3;
    @include grid100gap(1.5em);
  }
  &__item{
    @include grid100gap(.3125em);
    &._green{
      .case-card5__item-title:before{
        background-color: #65a141;
      }
    }
    &._blue{
      .case-card5__item-title:before{
        background-color: #0a61b0;
      }
    }
    &-title{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        right: 100%;
        margin-right: 2em;
        width: .1875em;
        height: 100%;
        background-color: $primary;
      }
    }
    &-desc{
      color: #2a2b2c;
    }
  }
}


@media screen and (max-width: 992px){
  .main-cases{
    .main2__inner{
      padding: 2.5em 0;
    }
    .main2__content{
      max-width: 29.5em;
    }
    .main2__img{
      width: 30em;
      left: 25em;
    }
  }

  .cases-nav{
    padding: 1em 0;
  }

  .cases2{
    &__items{
      gap: 1em;
    }
  }

  .case-card5{
    padding: .625em;
    border-radius: .5em;
    &__items{
      padding: 1em;
      border-radius: .25em;
      margin-top: 1em;
      gap: 1em;
    }
    &__item{
      @include grid100gap(.3125em);
      &-desc{
        font-size: .875em;
      }
      &-title{
        &:before{
          margin-right: .8125em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-cases{
    .main2__inner{
      padding-bottom: 12.625em;
    }
    .main2__img{
      left: 50%;
      bottom: 0;
      top: unset;
    }
  }
}
