.m-section{
  padding: 5.625em 0;
  &._border-top{
    border-top: 1px solid #EBEBEB;
  }
}

.is-wanted{
  &__inner{
    display: grid;
    grid-template-columns: 29.25em calc(100% - 29.25em - 1.875em);
    gap: 1.25em 1.875em;
  }
  &__nav,
  &__text{
    margin-top: 1.25em;
  }
}

.step-card{
  padding: 1.5625em 1.25em 1.5625em 3.125em;
  border-radius: .625em;
  @media screen and (min-width: 993px){
    transition: all .5s;
    &:hover{
      background-color: $black;
      color: $white;
      .step-card__desc{
        color: $white;
      }
      .step-card__icon{
        path{
          stroke: $white;
        }
      }
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    padding-right: .25em;
  }
  &__icon{
    width: 3.875em;
    height: 2em;
    @include center;
    svg{
      height: 100%;
      width: auto;
    }
  }
  &__desc{
    transition: all .3s;
    margin-top: .3125em;
    max-width: 41em;
    color: #6f7174;
  }
}

.cases{
  background-color: $grayBg;
  &__desc{
    color: #424345;
    max-width: 31.5em;
  }
}

.cases-slider{
  margin-top: 3.125em;
  .swiper-nav{
    display: none;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: 50% 50%;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        &:nth-child(1) .case-card,
        &:nth-child(2) .case-card{
          border-top: none;
        }
        &:nth-child(odd) .case-card{
          border-right: 1px solid #f2f2f2;
        }
      }
    }
    .case-card{
      border-top: 1px solid #f2f2f2;
    }
  }
}

.case-card{
  padding: 2.8125em;
  background-color: $white;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__desc,
  &__desc2{
    margin-top: .8125em;
  }
  &__desc{
    color: #4c4d50;
  }
  &__desc2{
    color: #3a3a3c;
    font-weight: 600;
  }
  &__nav{
    margin-top: auto;
    padding-top: .8125em;
  }
  &__btn{
    border-width: 1px;
    height: 2.625em;
    padding: 0 1.0625em;
    span{
      font-size: .875em;
    }
    svg{
      width: 1.5em;
      margin-left: .625em;
    }
    &:hover{
      path{
        fill: $white;
      }
    }
  }
}

.swiper-button{
  width: 3.125em;
  height: 3.125em;
  border-radius: 50%;
  @include center;
  margin: 0 .3125em;
  border: 1px solid #e7e7e8;
  background-color: $white;
  svg{
    width: .4375em;
  }
  &:not(.swiper-button-disabled):hover{
    background-color: $primary;
    border-color: $primary;
    path{
      fill: $white;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.875em 0;
  }

  .is-wanted{
    &__inner{
      grid-template-columns: 100%;
      position: relative;
      padding-bottom: 4.375em;
    }
    .h2{
      br{
        display: none;
      }
    }
    &__nav{
      margin: 0;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      justify-content: center;
    }
    &__btn{
      //height: 3.25em;
      padding: 0 2em;
      span{
        font-size: .875em;
      }
    }
    &__text{
      margin-top: .875em;
      max-width: 37.5em;
    }
  }

  .step-card{
    border-radius: 0;
    padding: .625em;
    border-bottom: 1px solid #e8e8e8;
    @media screen and (min-width: 577px){
      &__top{
        justify-content: unset;
        align-items: center;
      }
      &__icon{
        order: -1;
        width: auto;
        margin-right: .5625em;
        svg{
          width: 2.375em;
          height: auto;
        }
      }
      &__desc{
        margin-top: .5625em;
      }
    }
  }

  .cases{
    overflow: hidden;
    &__top{
      display: block;
    }
    &__desc{
      margin-top: .625em;
    }
  }

  .cases-slider{
    margin-top: 1.25em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 26.25em;
        padding-right: 1.25em;
      }
    }
  }

  .case-card{
    padding: 1.25em;
    border-radius: 1em;
    &__title{
      font-size: 1.0625em;
    }
    &__btn{
      height: 2.375em;
      span{
        font-size: .8125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .is-wanted{
    &__btn{
      width: 100%;
    }
  }

  .cases-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 100%;
      }
      &-nav{
        display: flex;
        justify-content: center;
        margin-top: 1em;
      }
      &-button{
        position: static;
        margin: 0 .3125em;
      }
    }
  }
}
