.guide3{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 21.5em - 3.125em) 21.5em;
    gap: 3.125em;
    align-items: center;
    margin-bottom: 2.5em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 2.8125em;
  }
  .h2{
    max-width: 13.65em;
  }
  .text{
    margin-top: 1.5em;
    color: $dark;
  }
  &__items{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
  &__nav{
    margin-top: 2.5em;
    justify-content: center;
  }
}

.guide-card3{
  background-color: $white;
  padding: 1.5em;
  border: 1px solid #f0f2f3;
  border-radius: .625em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    flex-shrink: 0;
    width: 1.5625em;
    height: 1.5625em;
    background-color: $black;
    border-radius: .25em;
    @include center;
    color: $white;
    font-weight: 600;
    margin-right: .625em;
    span{
      font-size: .75em;
    }
  }
  .text{
    margin-top: 1em;
    p,li{
      font-size: .9375em;
      line-height: 153%;
    }
    ul,ol{
      margin: 1em 0;
    }
  }
}

.law3{
  .law__desc{
    max-width: 100%;
    .text{
      color: $dark;
    }
  }
  .qa-card{
    .text{
      p{
        @include lastM(0 0 .625em);
      }
      ul,ol{
        margin: .625em 0;
      }
    }
  }
  .case-hint{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .guide3{
    .h2{
      br{
        display: none;
      }
    }
    &__grid{
      grid-template-columns: calc(100% - 20.625em - 1.25em) 20.625em;
      gap: 1.25em;
      margin-bottom: 1.5em;
    }
    &__img{
      border-radius: .625em;
    }
    .text{
      margin-top: 1em;
    }
    &__items{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .guide-card3{
    padding: .875em;
    border-radius: .5em;
    &__top{
      align-items: flex-start;
    }
    &__title{
      font-size: 1.0625em;
      line-height: 124%;
    }
    .text{
      margin-top: .625em;
      p,li{
        font-size: .875em;
        line-height: 150%;
      }
      ul,ol{
        margin: .625em 0;
        gap: .625em;
      }
    }
  }

  .law3{
    .h2 br{
      display: none;
    }
    .case-hint{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .guide3 {
    &__items,
    &__grid {
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
