.main{
  overflow: hidden;
  &__inner{
    padding: 5em 0;
    position: relative;
  }
  &__content{
    max-width: 47.5em;
  }
  &__desc{
    margin-top: 1.5625em;
    color: #4d4d4d;
    max-width: 39.375em;
  }
  &__nav{
    margin-top: 1.5625em;
  }
  &__btn{
    font-weight: 600;
  }
  &__img{
    position: absolute;
    top: 0;
    right: -6em;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}

.m-nav{
  display: flex;
}

.advantages{
  position: relative;
  color: $white;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 11.375em;
    left: 0;
    bottom: 0;
    background: #f4f4f4;
  }
  &__wrap{
    position: relative;
    z-index: 2;
    max-width: 1488px;
    margin: 0 auto;
    width: 100%;
    padding: 3.75em 0;
    background-color: #40464D;
    background-image: url(../img/advantages-bg.webp);
    background-size: cover;
    background-position: center;
    border-radius: 1.5em;
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em));
    gap: 3.75em;
  }
}

.advantage-card{
  &__icon{
    height: 3.375em;
    margin-bottom: .625em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .625em;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 4.75em 0;
    }
    &__content{
      max-width: 26.25em;
    }
    &__desc{
      margin-top: .9375em;
    }
    &__nav{
      margin-top: .9375em;
    }
    &__img{
      right: -1.25em;
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }

  .advantages{
    padding: 0 1.25em;
    &:before{
      height: 8.25em;
    }
    &__wrap{
      padding: 1.25em 0;
      border-radius: 1em;
    }
    &__inner{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .advantage-card{
    &__icon{
      height: 2.5em;
    }
    &__desc{
      margin-top: .4375em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    text-align: center;
    &__content{
      position: relative;
      z-index: 2;
    }
    &__nav{
      justify-content: center;
    }
    &__img{
      right: 50%;
      transform: translateX(50%);
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
  }

  .advantages{
    padding: 0 .9375em;
    &:before{
      height: 21.5em;
    }
    &__wrap{
      padding: 1.25em .3125em;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 1.125em;
    }
  }

  .advantage-card{
    display: flex;
    &__icon{
      height: unset;
      width: 2.5em;
      margin: 0 .75em 0 0;
      flex-shrink: 0;
      img{
        width: 100%;
        height: unset;
      }
    }
  }
}
