.main-human-rights{
  .main2__img{
    width: 33.25em;
    left: 49.5em;
  }
}

.human-rights{
  .h2{
    max-width: 16em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 75em;
  }
  &__nav{
    margin-top: 1.875em;
    justify-content: center;
  }
  &__btn{
    padding: 0 1.75em;
    height: 3.625em;
    span{
      font-size: .9375em;
    }
    svg{
      width: 1.125em;
      margin-left: .625em;
    }
    &:hover{
      path{
        stroke: $white;
      }
    }
  }
}

.human-rights-slider{
  margin-top: 2.5em;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
        gap: 1.875em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.human-rights-card{
  height: 100%;
  padding: 2.5em;
  border: 1px solid #F0F2F3;
  border-radius: 1.125em;
  display: flex;
  flex-direction: column;
  &__img{
    height: 5.5em;
    display: flex;
    align-items: center;
    margin-bottom: 1em;
    img{
      max-width: 15em;
      max-height: 100%;
    }
  }
  &__title{
    display: flex;
    align-items: center;
    min-height: 2.54em;
    @include truncate(2);
  }
  &__location{
    margin-top: .33em;
  }
  &__desc{
    margin-top: 1em;
    @include truncate(6);
  }
  &__nav{
    padding-top: 1em;
    margin-top: auto;
  }
  &__link{
    padding: 0 1.75em;
    height: 3.0625em;
    border-width: 1px;
    span{
      font-size: .9375em;
    }
  }
}




@media screen and (max-width: 992px){
  .main-human-rights{
    .main2__img{
      width: 22.5em;
      left: 25em;
    }
  }

  .human-rights{
    &__desc{
      margin-top: .625em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .human-rights-slider{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .5em));
          gap: 1em;
        }
      }
    }
  }

  .human-rights-card{
    padding: .875em;
    border-radius: .625em;
    &__img{
      height: 4.375em;
      margin-bottom: .3125em;
      img{
        max-width: 11.25em;
      }
    }
    &__title{
      font-size: 1.0625em;
      line-height: 124%;
      min-height: 2.48em;
    }
    &__desc,
    &__location{
      margin-top: .38em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-human-rights{
    .main2__inner{
      padding-bottom: 8.75em;
    }
    .main2__img{
      width: 13.75em;
      left: 50%;
      margin-top: -10em;
    }
    &__nav{
      display: none;
    }
  }

  .human-rights{
    overflow: hidden;
  }

  .human-rights-slider{
    margin-top: .875em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 17.875em;
        padding-right: 1em;
      }
    }
  }
}
