.text{
  color: #424345;
  h3{
    margin-bottom: 1em;
  }
  li,
  p{
    font-size: 1.0625em;
    line-height: 153%;
  }
  &>img{
    display: block;
    width: 100%;
    height: auto;
    border-radius: 1em;
  }
  ul,ol{
    margin: 1.25em 0;
    &:last-child{
      margin-bottom: 0;
    }
  }
  img,
  p{
    @include lastM(0 0 .75em);
  }
  hr{
    height: 1.25em;
    opacity: 0;
    display: block;
  }
  ul{
    @include grid100gap(.75em);
    padding-left: .75em;
    li{
      padding-left: .8em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background-image: unset;
        width: unset;
        height: unset;
        color: $primary;
      }
    }
  }
  ol{
    counter-reset: section;
    @include grid100gap(.75em);
    padding-left: .25em;
    li{
      position: relative;
      padding-left: 1.25em;
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        left: 0;
        top: 0;
        width: auto;
        height: auto;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}

.text._small,
.article{

  b{
    font-weight: 700;
  }

  &._list3{

  }
}


@media screen and (max-width: 992px){
  .text{
    li,
    p{
      font-size: .875em;
      line-height: 150%;
    }
    ul,ol{
      margin: .625em 0;
      gap: .625em;
    }
  }
}


