.header{
  position: relative;
  z-index: 20;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 12.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    &__item{
      @include lastM(0 .375em 0 0);
      width: 1.625em;
      height: 1.625em;
    }
  }
}

.burger{
  display: none;
}

.header-menu{
  .menu{
    display: flex;
  }
  .menu>.menu-item{
    height: 5.5em;
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    &>a{
      font-weight: 500;
      font-size: .875em;
      line-height: 121%;
    }
  }
  .menu-item-has-children{
    position: relative;
    &:after{
      content: '';
      width: .5625em;
      height: .9375em;
      background-image: url(../img/arrow-bottom4.svg);
      @include bgImgSize(100%);
      margin-left: .3125em;
      flex-shrink: 0;
      transition: all .3s;
    }
  }
  .sub-menu{
    .menu-item{
      a{
        font-size: .9375em;
        line-height: 120%;
        display: flex;
        align-items: center;
        padding: 1em 1.67em;
      }
      a:hover,
      &.current-menu-item a{
        background-color: $black;
        color: $white;
      }
    }
  }
  @media screen and (min-width: 993px){
    .menu-item-has-children{
      cursor: pointer;
      transition: all .3s;
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 2em;
        background-color: $white;
        left: 0;
        bottom: 0;
        z-index: 2;
        pointer-events: none;
        opacity: 0;
      }
      &:hover{
        background-color: $white;
        box-shadow: 0 .25em 3em rgba(0, 0, 0, 0.19);
        &:after{
          transform: rotate(180deg) translateY(-.125em);
        }
        &:before{
          opacity: 1;
        }
        .sub-menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .sub-menu{
      display: block !important;
      overflow: hidden;
      position: absolute;
      top: 100%;
      left: 0;
      white-space: nowrap;
      background-color: $white;
      box-shadow: 0 .25em 3em rgba(0, 0, 0, 0.19);
      border-radius: 0 1em 1em 1em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__inner{
      display: flex;
      align-items: center;
    }
    &__top{
      order: 2;
      margin-left: 1.875em;
    }
    &__bottom,
    .header__logo,
    &__bg{
      display: none;
    }
    .menu-item-service{
      &>img{
        width: 1.125em;
        margin-right: .3125em;
      }
      .sub-menu{
        padding: 1.25em 4.625em 1.25em 1.25em;
        display: grid !important;
        grid-template-columns: 1fr 1fr;
        gap: 1.25em 3.125em;
      }
      .menu-item{
        a{
          padding: 0;
          height: unset;
          display: flex;
          align-items: center;
          font-size: 1em;
          font-weight: 500;
        }
        img{
          width: 3.25em;
          margin-right: .625em;
          line-height: 125%;
        }
        a:hover,
        &.current-menu-item a{
          background-color: transparent;
          color: $primary;
          span{
            text-decoration: underline;
          }
        }
      }
    }
  }
  &__btn{
    padding: 0 1.0625em;
    height: 2.375em;
    span{
      font-size: .875em;
    }
  }
  .lang{
    display: none;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__phone{
    font-size: 1.125em;
    font-weight: 500;
    line-height: 120%;
  }
  .socials{
    margin-left: 1em;
  }
  .lang{
    margin-left: 1.25em;
  }
  &__btn{
    display: none;
  }
}

.lang{
  position: relative;
  &._open{
    .lang__show{
      border-bottom: $white;
      border-radius: 1em 1em 0 0;
      svg{
        transform: rotate(-180deg) translateY(-.125em);
      }
    }
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    border: 1px solid #eee;
    height: 2.375em;
    padding: 0 1em;
    border-radius: 3.375em;
    font-weight: 500;
    text-transform: uppercase;
    transition: all .3s;
    position: relative;
    z-index: 3;
    background-color: $white;
    &:hover{
      cursor: pointer;
    }
    span{
      font-size: .75em;
    }
    svg{
      width: .5em;
      margin-left: .25em;
    }
  }
  &__hidden{
    min-width: 12.5em;
    z-index: 2;
    position: absolute;
    right: 0;
    top: 100%;
    margin-top: -1px;
    background-color: $white;
    border: 1px solid #EEEEEE;
    border-radius: 1em 0 1em 1em;
    padding: .25em .25em .25em 0;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    max-height: 21em;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 0.25em;
      height: 0.25em;
    }
    &::-webkit-scrollbar-track {
      background: transparent;
      border-radius: 50em;
    }
    &::-webkit-scrollbar-thumb {
      background: #575757;
      border-radius: 50em;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    min-height: 3em;
    padding: .75em 1em;
    color: rgba($black,.8);
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    &:hover{
      color: $black;
      background-color: #F4F4F4;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.125em 0;
    &__left{
      display: flex;
      align-items: center;
    }
    &__logo{
      width: 7.875em;
    }
    .socials{
      &__item{
        width: 1.5em;
        height: 1.5em;
      }
    }
    &._open{
      .header-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-action{
    &__btn{
      margin-right: 1.875em;
      padding: 0 .75em;
      height: 1.75em;
      display: flex;
      span{
        font-size: .75em;
      }
    }
  }

  .header-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    z-index: 20;
    opacity: 0;
    pointer-events: none;
    &__inner{
      overflow-y: auto;
      width: 23.5em;
      height: $vh;
      position: relative;
      z-index: 3;
      background-color: $white;
      padding: 1em 1.25em;
      display: flex;
      flex-direction: column;
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba($black, 0.84);
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-bottom: 1.25em;
    }
    .header__left{
      margin-right: auto;
    }
    &__btn{
      padding: 0 .5em;
      height: 1.75em;
      span{
        font-size: .75em;
      }
    }
    .menu{
      display: block;
    }
    .menu>.menu-item{
      display: block;
      height: unset;
      padding: 0;
      &>a{
        font-weight: 600;
      }
    }
    .menu-item-has-children{
      position: relative;
      &:after{
        display: none;
      }
      &>a{
        padding: 0 .9em;
        font-size: .75em;
        line-height: 120%;
        text-transform: uppercase;
        font-weight: 600;
        border-bottom: 1px solid #f3f3f3;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 3em;
        &:after{
          content: '';
          width: .67em;
          height: 1em;
          background-image: url(../img/arrow-bottom4.svg);
          @include bgImgSize(100%);
          margin-left: .3125em;
          flex-shrink: 0;
          transition: all .3s;
        }
      }
      &._open{
        &>a{
          background: #f8f8f8;
          color: $black;
          &:after{
            transform: translateY(.2em) rotate(-180deg);
          }
        }
      }
    }
    .sub-menu{
      padding: .75em 1em;
      display: none;
      .menu-item{
        margin-bottom: .625em;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          background-color: transparent !important;
          color: $black !important;
          padding: 0;
          height: unset;
          font-size: .8125em;
          line-height: 154%;
        }
      }
    }
    &__bottom{
      display: flex;
      align-items: center;
      padding: 1em;
      width: calc(100% + 2.5em);
      margin: 0 0 0 -1.25em;
      border-top: 1px solid #f2f2f2;
      background: #fdfdfd;
    }
    &__phone{
      font-size: 1.125em;
      font-weight: 500;
    }
    .socials{
      margin-left: .625em;
    }
    .menu-item-service {
      &>img{
        display: none;
      }
      .sub-menu {
        .menu-item{
          img{
            display: none;
          }
        }
      }
    }
  }

  .lang{
    &._open {
      .lang__show {
        border-radius: .75em .75em 0 0;
      }
    }
    &__show{
      height: 1.75em;
      padding: 0 .75em;
    }
    &__hidden{
      font-size: .75em;
    }
  }

  .burger{
    width: 1.125em;
    @include center;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .8125em 0;
  }

  .header-action{
    &__phone,
    &__btn,
    .lang{
      display: none;
    }
  }

  .header-menu{
    .lang{
      display: block;
      margin-left: .5em;
      height: 1.75em;
      &._open{
        .lang__show{
          height: 1.875em;
        }
      }
      &__hidden{
        top: calc(100% + .125em);
      }
    }
  }
}
