.main-media-support{
  .main2__img{
    width: 35.625em;
  }
}

.media-info{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 36.5em) 33.5em;
    gap: 3em;
    align-items: center;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 2.8125em;
  }
  .text{
    margin-top: 1.5em;
  }
  &__items{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
}

.media-card{
  display: flex;
  color: $dark;
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.media-effect{
  .h2{
    max-width: 11em;
  }
  &__grid{
    margin-top: 1.875em;
  }
  &__text{
    padding: 2.5em;
    background-color: $grayBg;
    border-radius: 1.125em;
    p{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.effect-card{
  padding: 1.25em;
  border: 1px solid #f0f2f3;
  border-radius: 1.125em;
  &__top{
    min-height: 2.375em;
    display: flex;
    align-items: center;
    margin-left: calc(-1.25em - 1px);
    padding-left: 1.25em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .25em;
      height: 100%;
      background-color: #65a141;
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
  &__item{
    position: relative;
    padding-left: 2em;
    color: rgba($black,.8);
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/success-icon.svg);
      @include bgImgSize(100%);
      width: 1.375em;
      height: 1.375em;
      left: 0;
      top: 0;
    }
  }
  &._error{
    .effect-card__top{
      &:before{
        background-color: $primary;
      }
    }
    .effect-card__item{
      &:before{
        background-image: url(../img/error-icon.svg);
      }
    }
  }
}

.examples-slider2{
  .swiper-container{
    width: calc(100% + 1.875em);
  }
  .swiper-slide{
    width: 50%;
    padding-right: 1.875em;
  }
}

.case-card3{
  height: 100%;
  padding: 2.5em;
  border: 1px solid #f0f2f3;
  border-radius: 1.125em;
  background-color: $white;
  &__desc{
    margin-top: 1em;
    opacity: .8;
  }
}

.examples2{
  .examples__top{
    max-width: 35em;
  }
}


@media screen and (max-width: 992px){
  .main-media-support{
    .main2__img{
      width: 17.25em;
      left: 29em;
    }
    .main2__content{
      max-width: 29.5em;
    }
  }

  .media-info{
    &__grid{
      grid-template-columns: calc(100% - 20.625em - 1.25em) 20.625em;
      gap: 1.25em;
    }
    &__img{
      border-radius: .625em;
    }
    .text{
      margin-top: 1em;
    }
    @media screen and (min-width: 577px){
      &__items{
        margin-top: 1.25em;
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
        gap: 1.25em;
      }
    }
  }

  .media-card{
    &__text{
      font-size: .8125em;
      line-height: 162%;
    }
  }

  .media-effect{
    .h2{
      max-width: 100%;
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__text{
      grid-column: 1/-1;
      padding: 0;
      background-color: transparent;
      border-radius: 0;
      p{
        font-size: .9375em;
        line-height: 153%;
      }
    }
  }

  .effect-card{
    padding-top: 1em;
    padding-bottom: 1em;
    &__items{
      margin-top: 1em;
    }
  }

  .examples-slider2{
    margin-top: 1em;
    .swiper-container{
      width: calc(100% + .9375em);
    }
    .swiper-slide{
      padding-right: .9375em;
    }
    .swiper{
      &-nav{
        margin: 1em 0 0;
        position: static;
        justify-content: center;
      }
      &-button{
        margin: 0 .3125em;
      }
    }
  }

  .case-card3{
    border-radius: .625em;
    padding: .875em;
    &__desc{
      font-size: .875em;
    }
  }

  .examples2{
    .examples__top{
      max-width: 30em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-media-support{
    .main2__img{
      width: 17em;
      left: 50%;
      bottom: 0;
      top: unset;
    }
    .main2__inner{
      padding-bottom: 11em;
    }
  }

  .media-info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .media-effect{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .examples-slider2{
    .swiper-slide{
      width: 18.4375em;
    }
  }
}
