.about{
  margin-bottom: -2px;
  &__inner{
    display: grid;
    grid-template-columns: 40.25em calc(100% - 41.5em);
    gap: 1.25em;
    align-items: center;
  }
  &__col{
    padding: 4em 0;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__nav,
  &__text,
  &__text2{
    margin-top: 1.3125em;
  }
  &__text{
    color: #434445;
  }
  &__text2{
    color: #434445;
    @include grid100gap(.625em);
  }
  &__subtitle{
    color: $black;
    font-weight: 500;
    font-size: 1.0625em;
    line-height: 147%;
    letter-spacing: -0.02em;
  }
  &__btn{
    padding: 0 1.875em;
  }
}


@media screen and (max-width: 992px){
  .about{
    overflow: hidden;
    &__inner{
      grid-template-columns: 23.5em calc(100% - 23.5em);
    }
    &__col{
      padding: 1.875em 0;
    }
    &__col2{
      position: relative;
      height: 100%;
    }
    &__img{
      width: auto;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    &__nav,
    &__text,
    &__text2{
      margin-top: .625em;
    }
    &__subtitle{
      font-size: .9375em;
      line-height: 140%;
    }
    &__btn{
      padding: 0 1.5em;
      height: 3.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      display: none;
    }
  }
}
