.wanted-country{
  .main2__img{
    width: 49em;
  }
}

.country-info{
  padding-bottom: 5.625em;
  &__grid{
    display: grid;
    grid-template-columns: 19.625em calc(100% - 19.625em - 1.25em);
    gap: 1.25em;
  }
  .text{
    margin-top: 1.25em;
    li,
    p{
      font-size: .875em;
      line-height: 150%;
    }
    h4{
      margin-bottom: .9em;
    }
    h5{
      margin-bottom: .4em;
    }
    ul{
      gap: .4375em;
    }
  }
}

.country-nav{
  &._sticky,
  &__bg,
  &__show{
    display: none;
  }
  &__item{
    padding: .875em 1.25em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    color: #656565;
    font-weight: 500;
    border-bottom: 1px solid #cbcbcb;
    &:last-child{
      border-bottom: none;
    }
    &:after{
      content: '';
      flex-shrink: 0;
      width: .9375em;
      height: .9375em;
      background-image: url(../img/arrow-diagonal.svg);
      @include bgImgSize(100%);
      margin-left: .25em;
      margin-bottom: .4375em;
      display: none;
    }
    &._active{
      color: $black;
      &:after{
        display: block;
      }
      span {
        &:before {
          opacity: 1;
        }
      }
    }
    span{
      font-size: .9375em;
      line-height: 153%;
    }
    @media screen and (min-width: 577px){
      span{
        position: relative;
        &:before{
          content: '';
          position: absolute;
          z-index: -1;
          top: 1em;
          left: -1px;
          width: 4.5em;
          height: .2em;
          background-color: rgba($primary,.4);
          border-radius: .33em;
          transition: all .3s;
          opacity: 0;
        }
      }
    }
  }
}

.country-banner{
  position: relative;
  &__text{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 2.1875em;
    z-index: 2;
    color: $white;
    max-width: 27.5em;
    width: 100%;
  }
  &__img{
    border-radius: 1em;
    img{
      width: 100%;
      display: block;
      border-radius: inherit;
    }
  }
}

.examples{
  overflow: hidden;
  &__top{
    max-width: 47.5em;
    @include grid100gap(1.25em);
  }
  &__nav{
    justify-content: center;
    margin-top: 2.1875em;
  }
  &__btn{
    padding: 0 1.875em;
  }
}

.examples-slider{
  margin-top: 2.1875em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1em;
      width: 100%;
      height: auto;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.1875em;
      display: flex;
    }
    &-button{
      position: static;
      margin: 0 0 0 .625em;
    }
  }
}

.case-card2{
  border: 1px solid #f0f2f3;
  padding: 2.1875em;
  border-radius: 1.0625em;
  h3,h5{
    color: $black;
    font-weight: 600;
  }
  h3{
    max-width: 28em;
  }
  h5{
    margin-bottom: .625em;
  }
  p{
    font-size: 1em;
    line-height: 150%;
  }
  hr{
    height: .75em;
  }
}


@media screen and (max-width: 992px){
  .wanted-country{
    .main2__img{
      width: 32em;
    }
  }

  .country-info{
    padding: 1.875em 0;
    &__grid{
      grid-template-columns: 12.75em calc(100% - 14em);
    }
    .text{
      p{
        font-size: .8125em;
        line-height: 162%;
      }
      h4{
        font-size: 1.0625em;
        line-height: 124%;
        margin-bottom: .7em;
      }
    }
  }

  .country-nav{
    &__item{
      padding: .75em 0;
      &:after{
        opacity: 0;
        position: absolute;
        width: 0;
        height: 0;
      }
      span{
        font-size: .875em;
        line-height: 150%;
      }
    }
  }

  .country-banner{
    &__text{
      padding: 1.25em;
    }
    &__img{
      border-radius: .625em;
    }
  }

  .examples{
    &__top{
      max-width: 26.25em;
      gap: 1em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .examples-slider{
    margin-top: 1.5625em;
    .swiper{
      &-nav{
        margin-bottom: 2.0625em;
      }
    }
  }

  .case-card2{
    padding: 1.25em;
    h3{
      max-width: 30.125em;
    }
    p{
      font-size: .9375em;
      line-height: 153%;
    }
  }
}


@media screen and (max-width: 576px){
  .wanted-country{
    .main2__img{
      margin-top: -12em;
    }
  }

  .country-info{
    background-color: $white;
    position: relative;
    z-index: 2;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  ._fix-sticky-menu{
    .country-nav._sticky{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .country-nav{
    &._sticky{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 5;
      overflow-y: auto;
      display: block;
      .country-nav__items{
        position: relative;
        z-index: 4;
        box-shadow: 0 4px 30px 0 rgba(0, 0, 0, 0.11);
        background: #f1f1f1;
        border: none;
        border-radius: 0;
      }
      &:not(._open){
        .country-nav__bg{
          display: none;
        }
        .country-nav__items{
          opacity: 0;
          pointer-events: none;
          height: 0;
          overflow: hidden;
        }
      }
    }
    &._open{
      .country-nav__btn{
        transform: rotate(-180deg);
      }
    }
    &__show{
      z-index: 5;
      position: sticky;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: .75em 1em;
      box-shadow: 0 4px 29px 0 rgba(0, 0, 0, 0.33);
      background-color: $white;
      border-left: 3px solid $primary;
    }
    &__current{
      font-weight: 500;
      span{
        font-size: .875em;
        line-height: 121%;
      }
    }
    &__btn{
      margin-left: 3em;
      flex-shrink: 0;
      width: 1.875em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
    &__items{
      border: 1px solid #e6e6e6;
      border-radius: .625em;
    }
    &__item{
      padding: .75em .625em;
      align-items: flex-start;
      justify-content: unset;
      &:after{
        opacity: 1;
        display: block;
        order: -1;
        margin: .1875em .375em 0 0;
        position: static;
        width: .9375em;
        height: .9375em;
      }
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      display: block;
      background-color: rgba($black,.5);
    }
  }

  .country-banner{
    &__text{
      padding: .9375em;
    }
    &__img{
      img{
        min-height: 15em;
        object-fit: cover;
      }
    }
  }

  .examples-slider{
    .swiper{
      &-nav{
        margin: 1.5em 0 0;
        position: static;
        justify-content: center;
      }
      &-button{
        margin: 0 .3125em;
      }
    }
  }

  .case-card2{
    padding: .875em;
  }
}
