.about-main{
  padding: 2.5em 0 5em;
  background-image: url(../img/about-main-bg.svg);
  background-size: auto 100%;
  background-position: right top 0;
  background-repeat: no-repeat;
  &__desc{
    max-width: 38.375em;
    margin-top: 2.25em;
  }
}

.about-info{
  padding: 5em 0;
  background-color: $grayBg;
  &__grid{
    display: grid;
    grid-template-columns: 47.5em 29.625em;
    justify-content: space-between;
  }
  .h2{
    max-width: 11em;
  }
  .text{
    margin-top: 2em;
    color: $dark;
    p{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__items{
    @include grid100gap(3.375em);
  }
  &__item{
    @include grid100gap(.875em);
    &-num{
      font-weight: 500;
      font-size: 4.1875em;
      line-height: 81%;
      color: $primary;
    }
    &-text{
      font-size: .9375em;
      line-height: 153%;
      opacity: .9;
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding: 2.5em 0;
    background-image: url(../img/about-main-bg2.svg);
    &__desc{
      max-width: 100%;
      margin-top: 1em;
    }
  }

  .about-info{
    padding: 1.875em 0;
    &__grid{
      grid-template-columns: calc(100% - 12.625em - 1.25em) 12.625em;
      gap: 1.25em;
      align-items: center;
    }
    .text{
      margin-top: 1em;
    }
    &__items{
      gap: 1.25em;
    }
    &__item{
      gap: .625em;
      &-num{
        font-size: 2.8125em;
        line-height: 80%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    text-align: center;
    background-image: url(../img/about-main-bg3.svg);
    background-size: 100% auto;
    &__desc{
      max-width: 100%;
      margin-top: 1em;
    }
  }

  .about-info{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
