.main-refugee{
  .main2__img{
    width: 41.5em;
  }
}

.refugee-types{
  &__top{
    max-width: 38.5em;
  }
  &__grid{
    margin-top: 2.5em;
  }
  &__desc{
    color: #4d4d4d;
    margin-top: 1.5em;
  }
}

.refugee-card{
  padding: 2.5em;
  border: 1px solid #F0F2F3;
  border-radius: 1.125em;
  height: 100%;
  &__content{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__desc{
    opacity: .8;
  }
}

.m-icon{
  background-color: #F8F8F8;
  width: 4.125em;
  height: 4.125em;
  border-radius: 50%;
  @include center;
  img{
    width: 2em;
  }
}

.main-refugee2{
  .main2__img{
    width: 38.25em;
    left: 51.25em;
  }
}


@media screen and (max-width: 992px){
  .main-refugee{
    .main2__img{
      width: 26.875em;
      bottom: unset;
      top: 1.5em;
      left: 26.125em;
    }
    .main2__content{
      max-width: 31.25em;
    }
  }

  .refugee-types{
    &__top{
      max-width: 29em;
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__desc{
      margin-top: .5em;
    }
  }

  .refugee-card{
    padding: 1.25em;
    &__content{
      margin-top: .625em;
      gap: .25em;
    }
    @media screen and (min-width: 577px){
      display: flex;
      &__icon{
        margin-right: .625em;
        flex-shrink: 0;
      }
      &__title{
        br{
          display: none;
        }
      }
    }
  }

  .m-icon{
    width: 3.4375em;
    height: 3.4375em;
  }

  .main-refugee2{
    .main2__img{
      width: 25em;
      left: 25.5em;
      bottom: -3em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-refugee{
    .main2__inner{
      padding-bottom: 12.375em;
    }
    .main2__img{
      left: 50%;
      top: 100%;
      margin-top: -12em;
    }
  }

  .main-refugee2{
    .main2__inner{
      padding-bottom: 11.75em;
    }
    .main2__img{
      left: 50%;
      top: 100%;
      margin-top: -15.5em;
    }
  }
}
