.contacts{
  padding: 2.5em 0 5em;
  background-image: url(../img/contacts-bg.svg);
  background-repeat: no-repeat;
  background-position: right 4em top 0;
  background-size: auto 100%;
  &__inner{
    max-width: 54.625em;
    width: 100%;
  }
  &__items{
    background-color: $white;
    margin-top: 1.875em;
    border: 1px solid #ededed;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.contact-item{
  padding: 2.1875em;
  border-right: 1px solid #ededed;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &:last-child{
    border: none;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 120%;
    font-weight: 600;
  }
  &__link{
    margin-top: 1em;
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 500;
  }
  &__desc{
    font-size: .8125em;
    line-height: 120%;
    color: rgba($black,.6);
    margin-top: .5em;
  }
  .socials{
    margin-top: 1.1875em;
  }
}

.contacts-form{
  margin-top: 3.75em;
  &__fields{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
  }
  &__btn{
    margin-right: 1.5em;
    font-weight: 500;
    flex-shrink: 0;
  }
  .m-check{
    max-width: 20em;
  }
}

._full{
  grid-column: 1/-1;
}

.help-geography{
  background-color: $grayBg;
  padding: 2.5em 0;
  &__title{
    font-weight: 600;
    font-size: 1.8125em;
    line-height: 114%;
    letter-spacing: -0.02em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 45em;
  }
  .country-list,
  .search-field{
    margin-top: 1.875em;
  }
}

.search-field{
  position: relative;
  &__btn{
    position: absolute;
    height: 1.25em;
    @include center;
    top: 1.125em;
    right: 1em;
    svg,
    img{
      height: 100%;
      display: block;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  input{
    font-size: .875em;
    border: none;
    width: 100%;
    height: 4em;
    padding: 0 3em 0 1em;
    border-radius: .57em;
    box-shadow: 0 0 29px 0 rgba(0, 0, 0, 0.03);
    background-color: $white;
    color: $black;
    &::placeholder{
      color: #a5a5a5;
    }
  }
}

.country-list{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
  gap: 1.875em 2.5em;
  &__title{
    margin-bottom: .875em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &:after{
      content: '';
      height: 2px;
      width: 1em;
      flex-shrink: 0;
      background-color: $primary;
      margin-top: .25em;
    }
  }
  &__items{
    @include grid100gap(.25em);
  }
  &__more{
    grid-column: 1/-1;
    height: 2.375em;
    border-radius: .25em;
    background-color: $white;
    @include center;
    font-weight: 500;
    color: $primary;
    span{
      font-size: .875em;
      line-height: 129%;
      border-bottom: 1px dashed rgba($primary,.56);
    }
    &:hover{
      span{
        border-color: transparent !important;
      }
    }
    svg{
      width: .5625em;
      margin-left: .375em;
    }
    ._view2{
      display: none;
    }
  }
  @media screen and (min-width: 993px){
    &:not(._open){
      .country-item{
        &:nth-child(12)~.country-item{
          display: none;
        }
      }
    }
    &._open{
      .country-list__more{
        background-color: #E4E4E4;
        color: $black;
        span{
          border-color: rgba($black,.7);
        }
        svg{
          transform: rotate(-180deg);
        }
        path{
          fill: $black;
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }
}

.country-item{
  display: flex;
  align-items: center;
  padding: .3125em .625em;
  border: 1px solid #f2f3f3;
  background-color: $white;
  border-radius: .3125em;
  &:hover{
    color: $primary;
    &:after{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='9' viewBox='0 0 24 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.8536 4.85355C24.0488 4.65829 24.0488 4.34171 23.8536 4.14644L20.6716 0.964464C20.4763 0.769202 20.1597 0.769202 19.9645 0.964464C19.7692 1.15973 19.7692 1.47631 19.9645 1.67157L22.7929 4.5L19.9645 7.32843C19.7692 7.52369 19.7692 7.84027 19.9645 8.03553C20.1597 8.23079 20.4763 8.23079 20.6716 8.03553L23.8536 4.85355ZM0.5 5L23.5 5L23.5 4L0.5 4L0.5 5Z' fill='%23e51c38'/%3E%3C/svg%3E");
    }
  }
  &:after{
    content: '';
    flex-shrink: 0;
    width: 1.5em;
    height: .625em;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='9' viewBox='0 0 24 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.8536 4.85355C24.0488 4.65829 24.0488 4.34171 23.8536 4.14644L20.6716 0.964464C20.4763 0.769202 20.1597 0.769202 19.9645 0.964464C19.7692 1.15973 19.7692 1.47631 19.9645 1.67157L22.7929 4.5L19.9645 7.32843C19.7692 7.52369 19.7692 7.84027 19.9645 8.03553C20.1597 8.23079 20.4763 8.23079 20.6716 8.03553L23.8536 4.85355ZM0.5 5L23.5 5L23.5 4L0.5 4L0.5 5Z' fill='%23AAB1B9'/%3E%3C/svg%3E");
    @include bgImgSize(100%);
    margin-left: auto;
  }
  &__img{
    width: 1.3125em;
    flex-shrink: 0;
    @include center;
    margin-right: .5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    padding-right: 1em;
    font-weight: 500;
    font-size: .875em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 2.5em 0;
    background-image: url(../img/contacts-bg2.svg);
    background-size: 28em auto;
    background-position: right 0 top 0;
    &__items{
      margin-top: 1em;
    }
  }

  .contact-item{
    padding: 1.25em;
    &__link{
      margin-top: .6em;
    }
    .socials{
      margin-top: .625em;
      @media screen and (min-width: 577px){
        &__item{
          width: 2.625em;
          height: 2.625em;
          @include lastM(0 .625em 0 0);
        }
      }
    }
  }

  .contacts-form{
    margin-top: .9375em;
    &__fields{
      margin-top: .625em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      padding: 0 4em;
      height: 4em;
      span{
        font-size: 1.0625em;
      }
    }
    .m-check{
      max-width: unset;
    }
  }

  ._full-tablet{
    grid-column: 1/-1;
  }

  .help-geography{
    padding: 1.875em 0;
    &__title{
      font-weight: 500;
      font-size: 1.0625em;
      line-height: 118%;
      letter-spacing: -0.02em;
    }
    &__desc{
      margin-top: .625em;
      max-width: 100%;
    }
    .country-list,
    .search-field{
      margin-top: 1em;
    }
  }

  .search-field{
    position: relative;
    &__btn{
      top: .8125em;
    }
    input{
      height: 3.29em;
    }
  }

  .country-list{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .25em .625em;
    overflow-y: auto;
    max-height: 29.75em;
    padding-right: .625em;
    @include scroll;
    &__title{
      margin-bottom: .875em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      &:after{
        content: '';
        height: 2px;
        width: 1em;
        flex-shrink: 0;
        background-color: $primary;
        margin-top: .25em;
      }
    }
    &__more{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    background-image: url(../img/contacts-bg3.svg);
    background-size: 18.75em auto;
    background-position: center top 0;
    .h1{
      text-align: center;
    }
    &__items{
      margin-top: 2.5em;
      grid-template-columns: 100%;
    }
  }

  .contact-item{
    border-bottom: 1px solid #ededed;
    border-right: none;
  }

  .contacts-form{
    margin-top: 2.5em;
    &__fields{
      grid-template-columns: 100%;
    }
    &__nav{
      flex-direction: column;
    }
    &__btn{
      height: 4em;
      margin: 0 0 1.5em;
      padding: 0 4em;
      span{
        font-size: 1.0625em;
      }
    }
    .m-check{
      width: 100%;
    }
  }

  .country-list{
    grid-template-columns: 100%;
    max-height: 35em;
  }
}
