.services{
  padding: 5.625em 0;
  background-color: $grayBg;
}

._line{
  position: relative;
  display: inline-block;
  &:before{
    content: '';
    position: absolute;
    bottom: .15em;
    left: 0;
    width: 100%;
    height: .12em;
    border-radius: .1em;
    background-color: rgba($primary,.4);
  }
  span{
    position: relative;
    z-index: 2;
  }
}

.services-slider{
  margin-top: 3.125em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
        gap: 1.875em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.service-card{
  padding: 2.5em;
  background-color: $white;
  border-radius: 1.125em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  height: 100%;
  @media screen and (min-width: 993px){
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/service-bg.svg);
      background-position: left 7.5em top 5.5em;
      background-repeat: no-repeat;
      background-size: 41em auto;
      pointer-events: none;
    }
    &:hover{
      color: $white;
      box-shadow: 0 7px 35px 0 rgba(0, 0, 0, 0.04);
      background-color: $black;
      .service-card__icon{
        background: rgba(170, 177, 185, 0.13);
        path{
          stroke: $white;
        }
      }
      .service-card__desc{
        color: $white;
      }
      .service-card__btn{
        background-color: $primary;
        border-color: $primary;
        color: $white;
        &:hover{
          background-color: $primary;
        }
      }
    }
  }
  &__icon{
    transition: all .3s;
    background: #f8f8f8;
    width: 4.125em;
    height: 4.125em;
    border-radius: 50%;
    @include center;
    margin-bottom: .5em;
    svg{
      width: 2em;
      height: auto;
    }
  }
  &__title{
    display: flex;
    align-items: center;
    min-height: 2.54em;
  }
  &__desc{
    margin-top: 1.125em;
    color: #6a6d70;
    transition: all .3s;
  }
  &__nav{
    padding-top: 1.125em;
    margin-top: auto;
    display: flex;
  }
  &__btn{
    border-width: 1px;
  }
}


@media screen and (max-width: 992px){
  .services{
    padding: 1.875em 0;
    overflow: hidden;
  }

  .services-slider{
    margin-top: 1.25em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 18.4375em;
        padding-right: .9375em;
      }
    }
  }

  .service-card{
    padding: 1.25em;
    &__icon{
      width: 2.875em;
      height: 2.875em;
      margin-bottom: .375em;
      svg{
        width: 1.625em;
      }
    }
    &__desc{
      margin-top: .9375em;
    }
    &__nav{
      padding-top: .9375em;
    }
  }
}


