.request{
  padding: 3.75em 0 1.25em;
  background-color: $grayBg;
  position: relative;
  z-index: 2;
  &__grid{
    display: grid;
    grid-template-columns: 50.25em 28.375em;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
  }
  .contacts-form{
    margin: 0;
  }
  .text{
    p{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.free-help{
  &__inner{
    display: grid;
    grid-template-columns: 51em 30.25em;
    justify-content: space-between;
  }
  &__col{
    padding: 2.5em 0 5em;
  }
  &__col2{
    padding: .625em 0;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__desc{
    margin-top: 2.25em;
  }
}


@media screen and (max-width: 992px){
  .request{
    padding: 1.875em 0 0;
    &__grid{
      grid-template-columns: 100%;
    }
    .text{
      p{
        font-size: .875em;
      }
    }
    .h3{
      font-size: 1.375em;
      font-weight: 600;
      text-align: center;
    }
  }

  .free-help{
    &__inner{
      grid-template-columns: calc(100% - 21em) 20em;
    }
    &__col{
      padding: 2.5em 0;
    }
    &__col2{
      padding: 2em 0 0;
    }
    &__desc{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .free-help{
    text-align: center;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 2.5em 0 0;
    }
    &__col2{
      padding: .625em 0 1em;
    }
    &__img{
      width: 16.625em;
      margin: 0 auto;
    }
  }
}
