.footer{
  padding: 2.5em 0;
  background-color: $black;
  color: $white;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 11.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    &__item{
      width: 2.5em;
      height: 2.5em;
      @include lastM(0 .3125em 0 0);
      &:hover{
        rect{
          fill-opacity: 1;
        }
      }
    }
  }
}

.footer-info{
  &__hint{
    max-width: 18.5em;
    color: #6f7174;
    margin-top: 1.3125em;
  }
  &__c{
    margin-top: 1.3125em;
    color: #e5e7e9;
  }
  .socials{
    margin-top: 1.25em;
  }
}

.footer-contacts{
  min-width: 12.75em;
  &__top{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.125em;
  }
  &__phone{
    font-weight: 500;
    font-size: 1.25em;
    line-height: 120%;
    letter-spacing: -0.02em;
    white-space: nowrap;
  }
  .socials{
    margin-top: .5em;
    &__item{
      width: 1.625em;
      height: 1.625em;
    }
  }
  &__bottom{
    margin-top: 1.125em;
  }
  &__address{
    max-width: 12.75em;
    display: flex;
    align-items: flex-start;
    color: $gray;
    img{
      width: 1.125em;
      margin-right: .4375em;
    }
  }
}

.footer-nav{
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .5625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .64em);
    display: flex;
    align-items: flex-start;
    color: $gray;
    font-weight: 500;
    font-size: .875em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0;
    &__inner{
      display: grid;
      grid-template-columns: 21em 10.25em 10.25em;
      gap: 1em;
      padding-bottom: 3.5em;
      position: relative;
    }
    .h5{
      font-size: .9375em;
      line-height: 140%;
    }
  }

  .footer-info{
    &__bottom{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__c,
    .socials{
      margin: 0;
    }
    &__hint{
      margin-top: 1.25em;
      max-width: 15.3125em;
      .text14{
        font-size: .8125em;
        line-height: 162%;
      }
    }
  }

  .footer-contacts{
    grid-column: 1/-1;
    @media screen and (min-width: 577px){
      .h5{
        display: none;
      }
      &__wrap{
        display: grid;
        grid-template-columns: 21em calc(100% - 22em);
        gap: 1em;
      }
    }
    &__top,
    &__bottom{
      margin: 0;
    }
    &__top{
      flex-direction: row;
      align-items: center;
    }
    .socials{
      margin: 0 0 0 1.25em;
    }
    &__address{
      max-width: unset;
      align-items: center;
    }
  }

  .footer-nav{
    &__hidden{
      margin-top: .25em;
    }
    &__link{
      @include lastM(0 0 .31em);
      font-size: .8125em;
      line-height: 162%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      @include grid100gap(1em);
      padding-bottom: 5.75em;
    }
    &__logo{
      width: 10.375em;
      margin: 0 auto;
    }
  }

  .footer-info{
    &__bottom{
      flex-direction: column;
    }
    &__c{
      .text14{
        font-size: .8125em;
        line-height: 162%;
      }
    }
    .socials{
      order: -1;
      margin: 0 0 1em;
    }
    &__hint{
      display: none;
    }
  }

  .footer-contacts{
    &__wrap{
      margin-top: .625em;
    }
    &__top{
      align-items: flex-start;
    }
    &__phone{
      font-size: 1.0625em;
      letter-spacing: -0.02em;
    }
    .socials{
      margin-left: 2.125em;
    }
    &__bottom{
      margin-top: .625em;
    }
  }

  .footer-nav{
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding-bottom: .25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba($white,.07);
    }
    &__btn{
      @include center;
      width: 1.125em;
      height: 1.125em;
      margin-left: .25em;
      svg{
        width: 100%;
      }
    }
    &__hidden{
      display: none;
      margin-top: .5625em;
    }
    &__link{
      @include lastM(0 0 .7em);
    }
  }
}
