.consult{
  padding: 3.125em 0;
  background-color: $black;
  color: $white;
  background-image: url(../img/consult-bg.svg);
  background-repeat: no-repeat;
  background-size: auto 48.5em;
  background-position: right 2.5em center;
  &__inner{
    display: grid;
    grid-template-columns: 34.125em 26.5em;
    gap: .875em 1.875em;
  }
  &__desc{
    margin-top: 2.8125em;
    max-width: 28.75em;
    color: #e3e3e3;
  }
}

.consult2{
  background-color: #1e2327;
}

.consult-help{
  margin-top: 2.8125em;
  display: flex;
  align-items: center;
  &__icon{
    width: 2.625em;
    margin-right: .75em;
  }
  &__title{
    font-size: .875em;
    line-height: 164%;
    opacity: .5;
  }
  &__desc{
    font-weight: 500;
    font-size: .875em;
    line-height: 143%;
  }
}

.fg._white{
  label{
    color: rgba($white,.7);
  }
  input{
    border-color: $white;
    color: $white;
  }
}

.tel-input._white{
  .iti{
    &__arrow{
      background-image: url(../img/arrow-bottom2.svg);
    }
  }
  .iti__selected-dial-code{
    color: $white;
  }
}

.consult-form{
  border: 1px solid rgba($white,.03);
  padding: 2.1875em;
  border-radius: 1.5em;
  background: linear-gradient(-38.66deg, rgba(255, 255, 255, 0.01) 20.685%,rgba(255, 255, 255, 0.04) 98.004%);
  &__title{
    font-weight: 500;
    font-size: 1.4375em;
  }
  &__fields{
    @include grid100gap(1.5em);
    margin-top: 1.75em;
  }
  &__btn{
    width: 100%;
    margin-top: 1.875em;
  }

  .m-check{
    margin-top: 1.875em;
    label{
      color: $white;
    }
  }
}

.blog,
.cases{
  &__btn{
    height: 3.5625em;
    padding: 0 1.75em;
    font-weight: 500;
    span{
      font-size: .9375em;
    }
    svg{
      margin-left: .625em;
      width: 1.5em;
    }
    &:hover{
      path{
        fill: $white;
      }
    }
  }
}

.blog{
  padding: 5.625em 0;
  background-color: $grayBg;
  .tabs{
    margin-top: 1.25em;
  }
  .tab-content{
    margin-top: 3.125em;
  }
}

.blog-slider{
  margin-top: 3.125em;
  position: relative;
  &:first-child{
    margin-top: 0;
  }
  .swiper-nav{
    position: absolute;
    right: 0;
    bottom: 100%;
    display: flex;
    margin-bottom: 3.25em;
  }
  .swiper-button{
    position: static;
    margin: 0 0 0 .625em;
  }
  @media screen and (min-width: 993px){
    &._static-desktop{
      .swiper{
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
          gap: 1.875em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
    &:not(._static-desktop){
      .slider__wrap{
        overflow: hidden;
      }
      .swiper{
        &-container{
          width: calc(100% + 1.875em);
        }
        &-wrapper{
          align-items: stretch;
        }
        &-slide{
          height: auto;
          width: 25%;
          padding-right: 1.875em;
        }
      }
    }
  }
}

.blog-card{
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(56.3%);
    border-radius: .5em;
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.5em);
  }
  &__date{
    font-size: .8125em;
    line-height: 146%;
    letter-spacing: -0.02em;
    color: #747474;
  }
  &__desc{
    color: #6f7174;
    @include truncate(3);
  }
  &__nav{
    margin-top: auto;
    padding-top: .5em;
    display: flex;
  }
  &__link{
    font-weight: 500;
    font-size: .9375em;
    line-height: 113%;
    padding: 0 0 .125em;
    color: $primary;
    border-bottom: 1px solid rgba($primary, 0.12);
    &:hover{
      border-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding: 1.875em 0;
    background-image: url(../img/consult-bg2.svg);
    background-position: left 0 center;
    background-size: 28.375em auto;
    &__inner{
      align-items: center;
      justify-content: space-between;
      grid-template-columns: 19em 21.25em;
    }
    &__desc{
      margin-top: .875em;
    }
  }

  .consult-help{
    margin-top: .875em;
    &__icon{
      width: 1.75em;
      margin-right: .5625em;
    }
    &__title{
      font-size: .75em;
      line-height: 117%;
    }
    &__desc{
      font-size: .8125em;
      line-height: 131%;
    }
  }

  .consult-form{
    padding: 1.25em;
    border-radius: 1.0625em;
    &__title{
      font-size: 1.0625em;
      text-align: center;
    }
    &__fields{
      margin-top: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
    }
    .m-check{
      margin-top: .6875em;
    }
  }

  .blog,
  .cases{
    &__inner{
      position: relative;
      padding-bottom: 4.25em;
    }
    &__btn{
      white-space: nowrap;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      padding: 0 1.375em;
      height: 3em;
      span{
        font-size: .8125em;
      }
      svg{
        margin-left: .5em;
      }
    }
  }

  .blog{
    overflow: hidden;
    padding: 1.875em 0;
    .tab-content{
      margin-top: 1.25em;
    }
  }

  .blog2{
    .blog__inner{
      padding-bottom: 0;
    }
  }

  .blog-slider{
    margin-top: 1.25em;
    .swiper{
      &-nav{
        margin-bottom: 1.25em;
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 18.4375em;
        padding-right: .9375em;
      }
    }
  }

  .blog-card{
    &__img{
      padding-bottom: 62.1%;
    }
    &__date{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult{
    background-size: 100% auto;
    background-image: url(../img/consult-bg3.svg);
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      text-align: center;
    }
    .h2{
      br{
        display: none;
      }
    }
  }

  .consult-help{
    text-align: left;
    justify-content: center;
  }

  .consult-form{
    &__title{
      text-align: left;
    }
  }

  .blog-slider{
    .swiper-nav{
      position: static;
      margin-top: 1.25em;
      justify-content: center;
    }
    .swiper-button{
      margin: 0 .3125em;
    }
  }
}
