.main-judical-support{
  .main2__img{
    width: 37.25em;
    left: 55.25em;
  }
}

.info3{
  padding: 5em 0 2.5em;
}

.service2{
  padding: 2.5em 0;
  background-color: $grayBg;
  @media screen and (min-width: 993px){
    background-image: url(../img/service2-bg.svg);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 45.625em auto;
  }
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .375em));
    gap: .5em;
  }
}

.service-card2{
  padding: 1.25em 1.125em;
  background-color: $white;
  border: 1px solid #f0f2f3;
  border-radius: .625em;
  &__icon{
    height: 2em;
    display: block;
  }
  &__text{
    margin-top: .5em;
    @include grid100gap(.5em);
  }
}

.is-wanted3{
  @media screen and (min-width: 993px){
    padding-bottom: 2.5em;
    .step-card{
      padding: .9375em 1.25em;
    }
  }
}

.judical-advantages{
  padding: 5em 0;
  @media screen and (min-width: 993px){
    background-image: url(../img/judical-advantages-bg.svg);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: right top;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.5625em / 4));
    gap: 2.1875em;
  }
  .court__grid{
    margin-top: 3.125em;
  }
  .court-card{
    @media screen and (min-width: 993px){
      padding: 1.875em;
    }
    .text{
      color: #4C4D50;
      p{
        font-size: .875em;
      }
    }
  }
}

.advantage-card2{
  &__icon{
    background-color: $white;
  }
  &__content{
    margin-top: .3125em;
    @include grid100gap(.3125em);
  }
}


@media screen and (max-width: 992px){
  .main-judical-support{
    .main2__img{
      width: 24.25em;
      left: 27.625em;
    }
    .main2__content{
      max-width: 29.5em;
    }
  }

  .info3{
    padding: 1.875em 0;
  }

  .service2{
    padding: 1.875em 0;
    .h3{
      max-width: 22em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .service-card2{
    padding: 1.25em;
    background-color: $white;
    border: 1px solid #f0f2f3;
    border-radius: .625em;
    &__icon{
      height: 2em;
      display: block;
    }
    &__text{
      margin-top: .5em;
      @include grid100gap(.5em);
    }
  }

  .is-wanted3{
    .is-wanted__inner{
      padding-bottom: 0;
    }
    .is-wanted__steps{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    .step-card{
      padding: 0;
      border: none;
    }
  }

  .judical-advantages{
    padding: 1.875em 0;
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    .court__grid{
      margin-top: 1.875em;
    }
  }

  .advantage-card2{
    &__icon{
      width: 2.8125em;
      height: 2.8125em;
      img{
        width: 1.6875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-judical-support{
    .main2__inner{
      padding-bottom: 14.25em;
    }
    .main2__img{
      width: 18.375em;
      left: 50%;
      bottom: 0;
      top: unset;
    }
  }

  .is-wanted3{
    .is-wanted__steps{
      grid-template-columns: 100%;
    }
  }

  .judical-advantages{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    .court__grid{
      margin-top: 1.25em;
    }
  }

  .service2{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
