@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.gray-bg{
  background-color: $grayBg;
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.8125em;
    height: 2.8125em;
    @include lastM(0 .6875em 0 0);
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      rect{
        fill: $primary;
      }
      path{
        fill: $white;
      }
    }
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($black, 0.43);
    pointer-events: none;
    border-radius: inherit;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
  img{
    transition: all .6s;
  }
}


