.fg {
  position: relative;
  margin: 0;
  label{
    pointer-events: none;
    color: rgba($black,.7);
    display: block;
    font-size: 1em;
    line-height: 125%;
    margin-bottom: .375em;
    position: absolute;
    top: 1.125em;
    left: .625em;
    font-weight: 500;
    transition: all .2s;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    font-weight: 600;
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba(26, 26, 26, 0.6);
    padding: 1.5em .625em 0;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    &:focus{
      border-color: $primary;
    }
    &::placeholder {
      color: $gray;
    }
  }
  textarea{
    resize: none;
  }
  &._filled label,
  textarea:focus~label,
  input:focus~label{
    font-size: .75em;
    line-height: 117%;
    top: .83em;
    left: .83em;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.tel-input{
  z-index: 3;
  input{
    font-family: $font1;
    padding-top: 0;
  }
  .iti__search-input{
    display: none;
  }
  .iti .iti__selected-dial-code{
    font-weight: 600;
  }
  .iti--allow-dropdown .iti__flag-container:hover .iti__selected-flag{
    background-color: transparent;
  }
  .iti{
    width: 100%;
    color: $black;
    &__falg{
      box-shadow: 0 0 1px 0 #c2c2c2;
      border-radius: 2px;
    }
    &__arrow{
      border: none;
      width: 8px;
      height: 9px;
      background-image: url(../img/arrow-bottom.svg);
      @include bgImgSize(100%);
    }
    &__country-list{
      overflow-x: hidden;
      border: $border;
      border-radius: 0 0 .3125em .3125em;
      &::-webkit-scrollbar {
        width: 0.375em;
        height: 0.375em;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 0;
      }
      &::-webkit-scrollbar-thumb {
        background: $black;
        border-radius: 50em;
      }
    }
    &__highlight{
      background-color: #F0F0F0;
    }
    &__country{
      width: calc(100% + .375em);
      font-family: $font1;
      display: flex;
      align-items: center;
      .iti__dial-code{
        min-width: 3em;
        flex-shrink: 0;
        font-size: .875em;
        color: #90969C;
      }
      &-name{
        font-size: .875em;
        order: 2;
        font-weight: 500;
      }
    }
    &__divider,
    &__preferred{
      display: none;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    min-height: 1.5em;
    color: rgba($black,.7);
    span{
      font-size: .8125em;
      line-height: 162%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #e1e1e1;
      background-color: $white;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .8125em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .375em;
      background-image: url(../img/check.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #E9EDF2;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #E9EDF2;
    border-right: 1px solid #E9EDF2;
    color: $black;
    font-weight: 500;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


@media screen and (max-width: 992px){
  .tel-input{
    .iti{
      &__country-list{
        width: calc(100vw - 2.5em);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tel-input{
    .iti{
      &__country-list{
        width: calc(100vw - 1.875em);
      }
    }
  }
}
