.roles{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 28.25em) 26.25em;
    gap: 1.25em 2em;
    align-items: flex-end;
  }
  &__top{
    max-width: 41em;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.875em;
  }
}

.role-item{
  display: flex;
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .78em;
    color: #565656;
  }
}

.law2{
  .law__desc{
    max-width: 61em;
  }
}

.main-human-rights2{
  .main2__img{
    width: 48.1875em;
    left: 49em;
  }
}

.human-rights2{
  .human-rights-card__desc{
    -webkit-line-clamp: 3;
  }
}

.info2{
  padding: 5.625em 0;
}

.government{
  background-color: $grayBg;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 33.5em - 1.875em) 33.5em;
    gap: 1.875em;
    align-items: flex-end;
  }
  .h2{
    max-width: 12.25em;
  }
  &__items{
    margin-top: 1.25em;
  }
  &__img{
    width: 100%;
    border-radius: 1.875em;
    display: block;
  }
}

.government-item{
  padding: 1.25em;
  border-bottom: 1px solid #e1e1e1;
  @include grid100gap(.5625em);
  &:last-child{
    border-bottom: none;
  }
  &__desc{
    color: #6f7174;
  }
}


@media screen and (max-width: 992px){
  .roles{
    &__inner{
      grid-template-columns: 100%;
    }
    &__top{
      max-width: unset;
    }
    &__desc{
      margin-top: .625em;
    }
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    @media screen and (min-width: 577px){
      &__inner{
        position: relative;
      }
      &__top{
        min-height: 12.5em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: 16.625em;
      }
      &__col2{
        position: absolute;
        top: 0;
        right: 0;
        width: 16em;
        height: 12.5em;
        img{
          max-height: 100%;
          object-fit: cover;
        }
      }
    }
    &__img{
      border-radius: .625em;
    }
  }

  .role-item{
    &__icon{
      width: 1.5625em;
      margin-right: .625em;
    }
    &__desc{
      margin-top: .5em;
    }
  }

  .main-human-rights2{
    .main2__img{
      width: 20.25em;
      left: 26.5em;
    }
  }

  .info2{
    padding: 1.875em 0;
  }

  .government{
    &__grid{
      grid-template-columns: 100%;
      position: relative;
    }
    .h2{
      max-width: unset;
      font-size: 1.375em;
    }
    &__items{
      margin-top: 1.875em;
    }
    &__img{
      border-radius: .625em;
    }
    @media screen and (min-width: 577px){
      .government-item{
        &:nth-child(1),
        &:nth-child(2){
          width: calc(100% - 21.25em);
        }
      }
      &__col2{
        position: absolute;
        width: 20.625em;
        top: 3.5em;
        right: 0;
      }
    }
  }

  .government-item{
    padding: .625em;
    gap: .3125em;
    &__title{
      font-size: 1.0625em;
    }
  }
}


@media screen and (max-width: 576px){
  .roles{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .main-human-rights2{
    .main2__inner{
      padding-bottom: 12.5em;
    }
    .main2__img{
      width: 18.5em;
      left: 50%;
      margin-top: -13em;
    }
  }

  .government{
    &__items{
      margin-top: .625em;
    }
  }
}
