.blog-page{
  padding: 2.5em 0 5em;
  &__grid{
    margin-top: 2.5em;
  }
}

.blog-item{
  padding: 2.5em 0 5em;
  .page-top__desc{
    margin-top: .625em;
  }
  &__date{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 .5em;
    height: 2em;
    background-color: $black;
    border-radius: .5em;
    color: $white;
    margin-bottom: .625em;
  }
}

.article{
  margin-top: 2.5em;
  .case-hint{
    margin-bottom: 1em;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1em;
  }
  &__tag{
    margin: .625em .625em 0 0;
    padding: 0 .75em;
    border: 1px solid #EBEBEB;
    border-radius: .4375em;
    display: flex;
    align-items: center;
    height: 2.0625em;
    color: #454545;
    span{
      letter-spacing: -.02em;
    }
  }
  &__socials{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    .socials{
      margin-left: .5625em;
      &__item{
        width: 2.5em;
        height: 2.5em;
      }
    }
  }
}

.case-hint{
  padding: 2.5em;
  border-radius: 1.5em;
  background-color: #40464D;
  background-image: url(../img/case-hint-bg.svg);
  background-repeat: no-repeat;
  background-position: left 22.5em top 0;
  background-size: auto 100%;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__col{
    display: grid;
    grid-template-columns: 31.25em 24em;
    gap: .5em 3.625em;
    align-items: center;
  }
  &__desc{
    color: #C2C8CF;
  }
  &__btn{
    padding: 0 1.875em;
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    padding-bottom: 1.875em;
    &__grid{
      margin-top: 1.875em;
    }
  }

  .blog-item{
    padding-bottom: 1.875em;
    .page-top__desc{
      max-width: 100%;
    }
    &__date{
      padding: 0 .375em;
      height: 1.75em;
    }
  }

  .article{
    margin-top: 1em;
    hr{
      display: none;
    }
    .case-hint{
      margin-bottom: .75em;
    }
    &__tags{
      margin-top: .3125em;
    }
    &__socials{
      margin-top: 1em;
    }
  }

  .case-hint{
    background-image: url(../img/case-hint-bg2.svg);
    background-position: left 16.25em top 0;
    padding: 1.25em;
    border-radius: .625em;
    &__col{
      grid-template-columns: 100%;
      width: 24em;
      max-width: 100%;
    }
    &__btn{
      height: 4em;
      span{
        font-size: 1.0625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .case-hint{
    display: block;
    padding: 1.5em;
    border-radius: .625em;
    background-image: url(../img/case-hint-bg3.svg);
    background-position: center;
    &__col{
      gap: 1.6875em;
    }
    &__btn{
      width: 100%;
      margin-top: 1.6875em;
    }
  }

  .blog-item{
    .page-top{
      text-align: left;
    }
  }
}
