.main-wanted{
  .main2__img{
    width: 28.5em;
    left: 54em;
  }
}

.guide{
  .h2{
    max-width: 12em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 58em;
  }
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    gap: .9375em;
  }
}

.guide-card{
  padding: 1.25em 1.875em;
  border-radius: .625em;
  background-color: $white;
  transition: all .3s;
  display: flex;
  @media screen and (min-width: 993px){
    &:hover{
      background-color: $black;
      color: $white;
      .guide-card__desc{
        color: $white;
      }
      .guide-card__icon{
        path{
          stroke: $white;
        }
      }
    }
  }
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: .5625em;
    svg{
      width: 100%;
    }
  }
  &__desc{
    transition: all .3s;
    margin-top: .64em;
    max-width: 41em;
    color: #6f7174;
  }
}

.dont-know{
  padding-bottom: 2.5em;
  .h2{
    max-width: 13em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 58em;
  }
  &__grid{
    margin-top: 2.8125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
}

.guide-card2{
  padding: 2.5em;
  border: 1px solid #f0f2f3;
  border-radius: .625em;
  background: linear-gradient(178.29deg, rgba(229, 28, 56, 0.3) -83.47%,rgba(255, 255, 255, 0) 38.361%);
  display: flex;
  &._green{
    background: linear-gradient(178.29deg, rgba(101, 162, 65, 0.3) -83.47%,rgba(255, 255, 255, 0) 38.361%);
  }
  &__icon{
    width: 2em;
    margin-right: .625em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
    color: $dark;
  }
}

.find-out{
  position: relative;
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background-color: $grayBg;
  }
  .case-hint{
    position: relative;
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .main-wanted{
    .main2__img{
      width: 17.25em;
      left: 29.5em;
    }
    .main2__content{
      max-width: 29.5em;
    }
  }

  .guide{
    &__desc{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 1em;
    }
  }

  .guide-card{
    padding: .875em;
    &__icon{
      width: 1.625em;
      margin-right: .75em;
    }
  }

  .dont-know{
    padding-bottom: 1.875em;
    &__desc{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .guide-card2{
    padding: .875em;
    &__icon{
      width: 1.625em;
      margin-top: .3125em;
    }
    .h4{
      font-size: 1.0625em;
      line-height: 124%;
    }
    &__items{
      margin-top: .5em;
      gap: .3125em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-wanted{
    .main2__img{
      margin-top: -13.25em;
      left: 50%;
    }
    .main2__inner{
      padding-bottom: 14.5em;
    }
  }

  .dont-know,
  .guide{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .find-out{
    &:before{
      height: 4.625em;
    }
  }
}
