@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 2.25em;
    li{
        position: relative;
        display: inline;
        &:last-child{
            color: $gray;
            pointer-events: none;
        }
        &:not(:first-child) {
            margin-left: 2.25em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.1875em;
                height: 1px;
                background-color: rgba(#1d2636,.2);
                border-radius: 1px;
                top: .625em;
                right: 100%;
                margin-right: .625em;
            }
        }
    }
    a{
        font-size: .8125em;
        line-height: 162%;
    }
}

.page-top{
    &__desc{
        margin-top: 1.5em;
        color: #4D4D4D;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }

    .page-top{
        &__desc{
            margin-top: 1em;
            max-width: 29.5em;
        }
    }
}


@media screen and (max-width: 576px){
    .page-top{
        text-align: center;
    }
}
