.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        color: $white;
        background-color: $black;
        background-image: url(../img/modal-bg.svg);
        background-size: 33.5em auto;
        background-position: right bottom;
        background-repeat: no-repeat;
        width: 61em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 3.5em;
        border-radius: 1em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__close{
        width: 1.375em;
        @include center;
        position: absolute;
        top: 1.5em;
        right: 1.5em;
        opacity: .4;
        &:hover{
            opacity: 1;
        }
        svg{
            width: 100%;
        }
    }
}

.modal-form{
    margin-top: 3.125em;
    &__fields{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1em));
        gap: 1.5em;
    }
    .m-check{
        max-width: 20em;
        label{
            color: $white;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {
            padding-left: 1.5em;
            padding-right: 1.5em;
            //background-image: unset;
        }
    }

    .modal-form{
        margin-top: 1em;
        &__fields{
            grid-template-columns: repeat(2,calc(50% - .5em));
            gap: 1em;
        }
        .contacts-form__btn {
            padding: 0 1.5em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding-left: 1em;
            padding-right: 1em;
            padding-bottom: 1em;
            background-size: 100% auto;
        }
    }

    .modal-form{
        margin-top: 2em;
        &__fields{
            grid-template-columns: 100%;
        }
        .contacts-form__btn{
            padding: 0 1em;
            width: 100%;
        }
    }
}
