.tabs{
  display: flex;
}

.tab{
  flex-shrink: 0;
  height: 2.5em;
  padding: 0 .8125em;
  display: flex;
  align-items: center;
  position: relative;
  color: $black;
  transition: all .3s;
  @include lastM(0 .125em 0 0);
  border-radius: 56em;
  span{
    font-size: .9375em;
    line-height: 153%;
    border-bottom: 1px dashed $black;
  }
  ._count{
    font-size: .8125em;
    line-height: 162%;
    border-bottom: none;
    margin-left: .33em;
    color: $gray;
    opacity: 0.7;
  }
  &:hover{
    color: $primary;
    cursor: pointer;
    span{
      border-color: $primary;
    }
  }
  &._active{
    background: $primary;
    color: $white;
    pointer-events: none;
    span{
      border-color: transparent;
    }
    ._count{
      color: $white;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .tabs{
    overflow-x: auto;
    width: calc(100% + 2.5em);
    padding: 0 1.25em;
    margin-left: -1.25em;
    @include nullScroll;
  }

  .tab{
    height: 2em;
    padding: 0;
    @include lastM(0 .9375em 0 0);
    span{
      font-size: .8125em;
    }
    &._active{
      padding: 0 .6875em;
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    width: calc(100% + 1.875em);
    padding: 0 .9375em;
    margin-left: -.9375em;
  }
}
