.pagination{
  margin-top: 2.8125em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.625em;
    min-width: 3.625em;
    padding: 0 1.25em;
    border-radius: 0.25em;
    border: 1px solid #E6E8EB;
    @include center;
    margin: 0 .3125em;
    font-weight: 500;
    svg{
      width: 1.0625em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: $white;
      background-color: $primary;
      border-color: $primary;
      path{
        fill: $white;
      }
    }
    &._btn{
      svg{
        width: 1.4375em;
        margin: 0 .625em;
        &:first-child{
          margin-left: 0;
        }
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 1.875em;
    ._hide-tablet{
      display: none;
    }
    &__item{
      height: 3.125em;
      min-width: 3.25em;
      padding: 0 1em;
      &._btn{
        span{
          display: none;
        }
        svg{
          margin: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .pagination{

  }
}
